@mixin mobile {
  @media screen and (max-width: 480px) {
    @content;
  }
}
@mixin tablet {
  @media screen and (max-width: 768px) {
    @content;
  }
}
@mixin laptop {
  @media screen and (max-width: 1200px) {
    @content;
  }
}

*{
  margin: 0;
  padding: 0;
  font-family: 'Nunito Sans', sans-serif;
}
.formContainer {
  background-color: #a7bcff;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.formWrapper {
  background-color: white;
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 12px;
  padding: 20px 60px;
}
.logo {
  color: #5d5b8d;
  font-size: 25px;
  font-weight: bold;
}
.title {
  color: #5d5b8d;
  font-size: 16px;
}
label {
  display: flex;
  align-items: center;
  gap: 10px;
  color: #8da4f1;
  font-size: 12px;
  cursor: pointer;
}
::placeholder {
  color: rgb(175, 175, 175);
}
img {
  width: 42px;
}
form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}
button {
  background-color: #7b96ec;
  color: white;
  padding: 10px;
  cursor: pointer;
  border: none;
  font-weight: bold;
}
p {
  color: #5d5b8d;
  font-size: 12px;
  margin-top: 13px;
}
   input {
        padding: 15px;
        border: none;
        width: 250px;
        border-bottom: 1px solid #a7bcff;
        &::placeholder {
          color: rgb(175, 175, 175);
        }
      }
.home {
  margin: 0 0;
  padding: 0;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.chatImageName{
  display: flex;
  gap: 10px;
  img{
    border-radius: 50%;
  }
}
.container {
  height: 100%;
  width:100%;
  overflow: hidden;
  display: flex;
  @include tablet {
   width: 90%;
  }
}
.sidebar {
  flex: 0.7;
  background-color: #3e3c61;
}
.chat {
  flex: 2;
}
.navbar {
  display: flex;
  align-items: center;
  background-color: #2f2d52;
  height: 50px;
  padding: 10px;
  justify-content: space-between;
  color: #ddddf7;
}
.logo {
  font-weight: bold;
  @include tablet {
    display: none;
  }
}
.user {
  display: flex;
  gap: 10px;

  img {
    background-color: #ddddf7;
    height: 24px;
    width: 24px;
    border-radius: 50%;
    object-fit: cover;
  }
  button {
    background-color: #5d5b8d;
    color: #ddddf7;
    font-size: 10px;
    border: none;
    cursor: pointer;
    border-radius: 10px;
    @include tablet {
      position: absolute;
      bottom: 10px;
    }
  }
}

.search {

  border-bottom: 1px solid gray;

  width: 100%;
  .searchform {
    padding: 5px;
    input {
      background-color: transparent;
      border: none;
      color: white;
      outline: none;
    }
    & ::placeholder {
      color: light-grey;
    }
  }
}
.userChat {
  padding: 10px;
  display: flex;
  align-items: center;
  gap: 10px;
  color: white;
  cursor: pointer;

  &:hover {
    background-color: #2f2d52;
  }
  img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    object-fit: cover;
  }
  .userChatInfo {
    span {
      font-size: 19px;
      font-weight: 500;
    }
    p {
      font-size: 14px;
      color: lightgrey;
    }
  }
}

.chat {
  flex: 2;
  .chatInfo {
    height: 50px;
    background-color: #5d5b8d;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    color: lightgray;
    img{
      border-radius: 40px;
    }
  }

  .chatIcons {
    display: flex;
    gap: 16px;
    img {
      height: 30px;
      width: 41px;
      cursor: pointer;

    }
  }
  .chats p{
    color: black;
  }
  .messages {
    background-color: #ddddf7;
    padding: 10px;
    overflow: scroll;
    height: calc(100% - 160px);
    .message {
      display: flex;
      gap: 20px;
      margin-bottom: 20px;
      .messageinfo {
        display: flex;
        color: grey;
        font-weight: 325px;
        flex-direction: column;
        img {
          width: 40px;
          object-fit: cover;
          height: 40px;
          border-radius: 50%;
        }
      }
      .messageContent {
        max-width: 80%;
        display: flex;
        gap: 10px;
        flex-direction: column;
        p {
          background-color: white;
          padding: 10px 20px;
          border-radius: 0px 15px 15px 15px;
          max-width: max-content;
          font-size: 18px;
        }
        img {
          width: 50%;
        }
      }
      &.owner {
        flex-direction: row-reverse;
        .messageContent {
          align-items: flex-end;
          p {
            background-color: #8da4f1;
            color: white;
            border-radius: 15px 0 15px 15px;
          }
        }
      }
    }
  }
  .input {
    height: 50px;
    background-color: #2f2d52;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    input {
      width: 100%;
      background-color: #2f2d52;
      border: none;
      outline: none;
      color: white;
      font-size: 18px;
    }
    &::placeholder {
      color: lightgray;
    }
    button{
      border-radius: 20%;
    }
  }
  .send {
    display: flex;
    align-items: center;
    gap: 10px;

    img {
      height: 24px;
      cursor: pointer;
    }
    button {
      cursor: pointer;
      border: none;
      padding: 10px;
      color: white;
      background-color: #8da4f1;
    }
  }
}
